import React, {useState, useEffect} from 'react';
import authorizeMethods from '../../Firebase/authmethods';

export const AuthContext = React.createContext()

const AuthProvider = (props) => {
  const initState = {
    email: '', 
    password: '', 
    firstName: '', 
    lastName: '',
    aptmtDesc: '',
    aptmtStart: '',
    aptmtEnd: '',
    aptmtHStyle: '',
    aptmtNStyle: '',
    aptmtCustId: '',
    aptmtEmpId: ''
  };
  const [inputs, setInputs] = useState(initState);
  const [errors, setErrors] = useState([]);
  const [token, setToken] = useState(null);


  const handleSignups = () => {
    authorizeMethods.signup(inputs.email, inputs.password, setErrors, setToken);
    //console.log(errors, token); //Uncomment incase there are errors;
  };

  const handleLogins = () => {
    authorizeMethods.signin(inputs.email, inputs.password, setErrors, setToken);
    //console.log(errors, token); //Uncomment in case there are any errors
  };

  const handleSignouts = () => {
    authorizeMethods.signout(setErrors, setToken);
  };

  return (
    <AuthContext.Provider
    value={{
      handleSignups,
      handleLogins,
      token,
      inputs,
      setInputs,
      errors,
      setErrors,
      handleSignouts,
    }}>
      {props.children}
    </AuthContext.Provider>
  );
};

export default AuthProvider;